import React from 'react'
import edsolver from '../img/edsolver.png'
import lalsci from '../img/lalsci.png'
import ni from '../img/ni.png'


export default () => {
  return (
    <div className="project">
      {/* <h1>This is project</h1> */}
      <div className="project-edsolver">
        <img src={edsolver} alt="edsolver"/>
        <div>
          <a  href="http://www.edsolver.com" target="_blank"  rel="noopener noreferrer"><h2 className="project-title edsolver-des">EDSolver</h2></a>
           <p>EDSolver </p>
           <div className="edsolver-des">
              <span>Vue.js</span>
              <span>Node.js</span>
              <span>MongoDB</span>
              <span>D3.js</span>
           </div>
      
        </div>
      </div>
      <div className="project-separator"></div>

        <div className="project-edsolver">
        <img src={ni} alt="ni"/>
        <div>
          <a href="http://www.ee.cuhk.edu.hk/~nzhao/#/" target="_blank"  rel="noopener noreferrer"> <h2 className="project-title">Ni's Website</h2></a>
           <p>single page application </p>
           <div className="edsolver-des">
              <span>React.js</span>
              <span>Apache</span>
           </div>
      
        </div>
      </div>
      <div className="project-separator"></div>

         <div className="project-edsolver">
        <img src={lalsci} alt="lalsci"/>
        <div>
           <a href="http://www.lalsci.com" target="_blank"  rel="noopener noreferrer"><h2 className="project-title">LALSCI</h2></a>
           <p>LALSCILAL(Laser Ablation in Liquids) SCI(Science Citation Index)LAL </p>
           <div className="edsolver-des">
              <span>React.js</span>
              <span>Django</span>
              <span>Elasticsearch</span>
             
           </div>
      
        </div>
      </div>
      <div className="project-separator"></div>
    </div>
  )
}


